export function cleanInput(input) {
  input = input.replace(/[x/+\-(.^]*$/, "");
  input = input.replace(/x/g, "*");
  input = input.replace(/\^/g, "**");
  input += ")".repeat(netParentheses(input));
  return input;
}

export function netParentheses(input) {
  let openCount = 0;
  let closedCount = 0;
  for (let char of input) {
    switch (char) {
      case "(":
        openCount++;
        break;
      case ")":
        closedCount++;
        break;
    }
  }
  return openCount - closedCount;
}
