import React from "react";

let Buttons = (props) => {
  const buttons = [
    "C",
    "( )",
    "^",
    "/",
    "7",
    "8",
    "9",
    "x",
    "4",
    "5",
    "6",
    "-",
    "1",
    "2",
    "3",
    "+",
    "0",
    ".",
    "⇦",
    "=",
  ];
  const handleClick = ({ target }) => {
    props.handleButtonClick(target.id);
  };
  return (
    <div className="calc-button-container">
      {buttons.map((value) => (
        <div
          onClick={handleClick}
          id={value}
          className="calc-button"
          key={value}
        >
          {value}
        </div>
      ))}
    </div>
  );
};
export default Buttons;
