import React, { useState, useEffect } from "react";
import Buttons from "./Buttons";
import Display from "./Display";
import { cleanInput, netParentheses } from "../utils";

let Calculator = () => {
  const [input, setInput] = useState("");
  useEffect(() => {
    const handleKeyDown = ({ key }) => {
      let result = key;
      switch (key) {
        case "Backspace":
          result = "⇦";
          break;
        case "(":
        case ")":
          result = "( )";
          break;
        case "c":
          result = "C";
          break;
        case "*":
        case "X":
          result = "x";
          break;
        case "Enter":
          result = "=";
          break;
      }
      handleButtonClick(result);
    };
    document.addEventListener("keydown", handleKeyDown);
    return () => document.removeEventListener("keydown", handleKeyDown);
  });
  function handleButtonClick(clickedButton) {
    if (input.length > 25 && !"C=⇦".includes(clickedButton)) return;
    if (clickedButton >= "0" && clickedButton <= "9") {
      setInput((prevInput) => {
        return (
          prevInput + (prevInput.slice(-1) === ")" ? "x" : "") + clickedButton
        );
      });
      return;
    }
    switch (clickedButton) {
      case "x":
      case "/":
      case "+":
      case "-":
      case "^":
        if (input) {
          if (["x", "/", "+", "-", "^"].includes(input.slice(-1))) {
            setInput((prevInput) => prevInput.slice(0, -1) + clickedButton);
            return;
          } else {
            setInput((prevInput) => prevInput + clickedButton);
          }
        }
        break;
      case "=":
        if (input && isNaN(cleanInput(input))) {
          setInput((prevInput) => {
            let result;
            try {
              result = eval(cleanInput(prevInput)).toString();
            } catch (error) {
              result = prevInput;
            }
            return result;
          });
        }
        break;
      case "C":
        setInput("");
        break;
      case "⇦":
        setInput((prevInput) => prevInput.slice(0, -1));
        break;
      case "( )":
        if (
          netParentheses(input) &&
          !["x", "/", "+", "-", "(", "^"].includes(input.slice(-1))
        ) {
          setInput((prevInput) => prevInput + ")");
        } else {
          setInput((prevInput) => {
            return prevInput + (/[0123456789).]$/.test(prevInput) ? "x(" : "(");
          });
        }
        break;
      case ".":
        if (input.slice(-1) !== ".") {
          setInput((prevInput) => {
            return (
              prevInput +
              (prevInput.slice(-1) === ")" ? "x" : "") +
              clickedButton
            );
          });
        }
        break;
    }
  }
  return (
    <div className="calculator">
      <Display input={input} />
      <Buttons handleButtonClick={handleButtonClick} />
    </div>
  );
};
export default Calculator;
