import React from "react";
import { cleanInput } from "../utils";

let Display = ({ input }) => {
  let result;
  try {
    result = isNaN(cleanInput(input)) ? eval(cleanInput(input)).toString() : "";
  } catch (error) {
    result = "Invalid Input";
  }
  return (
    <div className="display-container">
      <div className="display">
        <div className="display-line" style={{ fontSize: getFontSize(input) }}>
          &nbsp;
          {input}
        </div>
        <div className="display-line" style={{ fontSize: getFontSize(result) }}>
          &nbsp; {result}
        </div>
      </div>
    </div>
  );
};
export default Display;

function getFontSize(input) {
  return (
    Math.floor(
      Math.max(Math.min(60, 60 - (20 * (input.length - 17)) / 9), 40)
    ) + "px"
  );
}
